* {
  margin: 0;
  padding: 0;
}



.side-nav-icon{
  font-size: 1.5vw;
  color: #fff;
  transition-property: opacity, font-size;
  transition-delay:3s,0s;
  transition-duration: 1s;
}
.nav-bar-top{
  position: fixed;
  right:0;
  z-index:6;
  display:flex;
  align-items:center;
}
.nav-section:hover{
  color:#ac3d15;
  transition: all 0.5s;
  font-size: 1.5rem;
}
.top-nav-divider{
  height:1px;
  width:1rem;
  background-color:white;
  
  transition-delay: 1s;
}
.nav-section {
  
  color: #fff;
  text-decoration: none;
  word-spacing: 2px;
  font-weight: bolder;
  transition-property: opacity, font-size;
  transition-duration: 1s;
  transition-delay: 4s, 0s;
  display: flex;
  font-size: 0.75vw;
  justify-content: center;
  align-items: center;
  height: 8vh;
  width: 8vw;
  text-transform: uppercase;
  
  transform-origin: top left;
  
  
}
.side-nav-icon:hover{
  transition: all 0.5s;
  color:#ac3d15;
  font-size: 2rem;
}

.phone-number{
  font-size: 1rem;
  color: #fff;
  transition: opacity 1s ease-in-out;
  width:7vw;
  text-align: center;
}
.side-nav{
  position: fixed;
  bottom:0vw;
  z-index: 4;
  width:3vw;
  height:40vh;
  
  margin:1rem;
  padding:1rem;
  
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.projects-carousel{
  z-index: 9;
  width:100%;
  height:100%;
 
}
.carousel-slide{
}
.each-slide-effect > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  height: 350px;
  z-index: 10;
}

.each-slide-effect span {
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
  z-index:10;
}
.card-container {
  display: flex;
  flex-wrap: wrap;
  
  justify-content: space-evenly;
  width:80%;
  height:80%;
  align-content: center;
  align-items:center;
}


.project-title{
  font-size: 5rem;
  position: absolute;
  color: #1ecbe1;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bolder;
  z-index: 5;
  
}
.ant-carousel{
  z-index: 10;
}
.card:hover{
  width:28%;
  height:83%;
  transition: all 1s ease-in-out;
  background-color: #1ecbe1;
  color:#fff;
}
.card {
  display:flex;
  text-decoration: none;
  
  color:#031923;
  justify-content: center;
  align-items: center;
  opacity: 0;
  width: 25%;
  height: 80%;
  margin:20px;
  background-color: #020D11;
  border-radius: 8px;
  text-align: center;
  line-height: 2rem;
  font-size: 2rem;
  
  
  transition: all 1s ease-in-out;
}
.side-nav .in-viewport{
  opacity: 1;
}
.intro .in-viewport{
  opacity: 1;
}

.card-container .in-viewport {
  opacity: 1;
  
}
.loading-screen{
  width:100%;
  height: 100%;
  background-color:#024744;
  position: fixed;
  z-index:7;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  animation-name:move-loading-screen;
  animation-duration:1s;
  animation-fill-mode: forwards;
  animation-delay: 3s;
}
.loading{
  margin: 0;
  padding: 0;
  z-index:8;
}
@keyframes move-loading-screen {
  to {
    transform: translateX(100%);
  }
}
.projects{
  top:200vh;
  position:absolute;
  z-index: 3;
  height: 100%;
  overflow: hidden;
  width:100%;
 display:flex;
 align-items: center;
  overflow: hidden;
  justify-content: space-around;
}
.show {
  opacity: 1;
}

.hide {
  opacity: 0;
}
.main-background {
  height: 100%;
  width: 100%;
  background: linear-gradient( #006ab6, #024744,  #031923);
  position: absolute;
}

.portrait-holder{
  display: grid;
  width:20vw;
  height: 20vw;
}

.portrait{
  background: url(assets/portrait.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  
  position: relative;
  grid-row: 1;
  grid-column: 1;
